import { setBoxShouldRefresh } from "@store/AppSlice";
import { useAppDispatch } from "@store/Store";
import React, { useEffect, useState } from "react";
import AddWidget from "./Components/AddWidget/AddWidget";
import BoxContainer from "./Components/boxContainer";
//Icons
import EditWidgetDrawer from "./Components/EditWidget/EditWidgetDrawer";
import WidgetContainer from "./Components/WidgetContainer/WidgetContainer";
import { resetBoxSlice } from "./Redux/BoxSlice";
import { setBoxUIEditModePrev } from "./Redux/BoxUISlice";


export default function Box() {
  const dispatch = useAppDispatch();
  const [isBoxStillFetching, setBoxFetchingLocal] = useState(true);

  useEffect(() => {
    return () =>{ 
      dispatch(resetBoxSlice());
      dispatch(setBoxShouldRefresh(false));
      dispatch(setBoxUIEditModePrev(false));
    }
  }, []);

  return (
    <React.Fragment>
      <BoxContainer onChangeFetchingStatus={value => setBoxFetchingLocal(value)}>
        <AddWidget />
        <EditWidgetDrawer /> 
      </BoxContainer>
      {!isBoxStillFetching && <WidgetContainer />}
    </React.Fragment>
  );
}