import { Grid, Typography } from '@material-ui/core';
import { Skeleton } from '@material-ui/lab';
import ApiService, { generateCancelToken } from '@services/apiService';
import { setFetchingMessage, setFetchingState, setFetchingStatus } from '@store/AppSlice';
import { useAppDispatch, useTypedReduxSelector } from '@store/Store';
import React, { useEffect, useState } from 'react';
import { matchPath } from 'react-router';
import BoxConnectionManager from '../BoxConnectionManager';
import { setBoxState } from '../Redux/BoxSlice';
import { setBoxUIShouldRefresh } from '../Redux/BoxUISlice';
import BoxHeader from './boxHeader';


export default function BoxContainer(props) {
  const dispatch = useAppDispatch();
  const boxState = useTypedReduxSelector(state => state.box);
  const boxUIState = useTypedReduxSelector(state => state.boxPageUI);
  const widgetState = useTypedReduxSelector(state => state.widget);
  const apiCancelToken = generateCancelToken();
  const api = new ApiService(apiCancelToken);
  const [isBoxFound, setBoxFound] = useState(true);
  const [boxFetchingStatus, setFetchingStatusLocal] = useState(true);
  const connection = BoxConnectionManager();

  const getUrlParams = matchPath(window.location.pathname, {
    path: '/box/:uniqueBoxToken',
    exact: false,
    strict: false,
  });

  /**
   * Fetching new box data by using its token
   * 
   * @param {string} uniqueToken 
   * @returns Promise|void
   */
  const fetchBox = async (uniqueToken) => {
    if (uniqueToken === null || uniqueToken === undefined) {
      return Promise.reject();
    }
    setFetchingStatusLocal(true);
    
    try {
      const getBox = await api.getBox(uniqueToken);
      const boxData = getBox.data.data;
      dispatch(
        setBoxState({
          boxId: boxData.boxId,
          boxToken: boxData.boxToken,
          boxDeviceToken: boxData.boxDeviceToken,
          boxName: boxData.boxName,
          boxTagName: boxData.boxTagName,
          boxDescription: boxData.boxDescription,
          boxActiveStatus: boxData.boxActiveStatus,
          boxSettings: boxData.boxSettings,
          boxWidgets: boxData.boxWidgets,
        })
      );
      setBoxFound(true);
      setFetchingStatusLocal(false);

    } catch (error) {
      console.log(error);
      setBoxFound(false);
      setFetchingStatusLocal(false);
    }
  }

  /**
   * Update and save into database
   * widget layout that user made
   * 
   * 
   * @returns Promise
   */
  const updateWidgetLayout = async () => {

    if (boxState.boxToken === '' && localStorage.getItem(`boxLayout-${boxState.boxToken}`) === null) {
      return Promise.reject();
    }

    try {
      let dataUpdate = {
        boxToken: boxState.boxToken,
        widgetUniqueId: widgetState.widgetId,
        widgetLayout: localStorage.getItem(`boxLayout-${boxState.boxToken}`) || JSON.stringify({}),
        widgetBoxLayout: localStorage.getItem(`boxLayout-${boxState.boxToken}-allLayouts`) || JSON.stringify({}),
      }
      dispatch(setFetchingStatus(true));
      const updateLayout = await api.updateWidgetBoxLayout(dataUpdate, apiCancelToken);
      if (updateLayout.data.header.status === 'success') {
        dispatch(
          setFetchingState({
            message: 'layout has been saved',
            severity: 'success',
            isOpen: true,
            fetchingStatus: false,
          })
        );
        dispatch(setBoxUIShouldRefresh(true));

      }
    } catch (error) {
      dispatch(
        setFetchingState({
          message: 'Something went wrong, please try again!',
          severity: 'error',
          isOpen: true,
          fetchingStatus: false,
        })
      );
      console.error(error);
    }
  }

  /**
   *  --------------
   *  Save layout into database when switch is false
   *  --------------
   */
  useEffect(() => {
    if (!boxUIState.isBoxEditMode && boxUIState.isBoxEditModePrev) {
      updateWidgetLayout();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [boxUIState.isBoxEditMode]);


  /**
   *  --------------
   *  Get box data based on shouldRefresh
   *  --------------
   */
  useEffect(() => {
    if ((boxUIState.shouldBoxRefresh === true) && (boxUIState.isBoxEditMode === false) && boxUIState.isBoxEditModePrev) {
      dispatch(setFetchingMessage({
        message: 'Refreshing your dashbox..',
        severity: 'info',
        isOpen: true,
      }));
      dispatch(setBoxUIShouldRefresh(false));
      fetchBox(boxState.boxToken);
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [boxUIState.shouldBoxRefresh, boxUIState.isBoxEditMode]);


  useEffect(() => {
    props.onChangeFetchingStatus(boxFetchingStatus);
  }, [boxFetchingStatus]);

  /**
   *  --------------
   *  On First Loading Reset
   *  when there are some reasons
   *  --------------
   */
  useEffect(() => {
    if (getUrlParams.params.uniqueBoxToken !== undefined) {
      console.log('box fetching..');
      fetchBox(getUrlParams.params.uniqueBoxToken);
    }

    return () => {
      apiCancelToken.cancel();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <React.Fragment>
      <Grid container spacing={1}>
        {boxFetchingStatus ? <SkeletonLoadingHeader /> : <BoxHeader />}
      </Grid>
      {boxFetchingStatus ? <SkeletonLoadingBody /> : isBoxFound ? props.children :
        <Grid alignContent="center" alignItems="center" justify="center" container spacing={3}>
          <Grid item xs={12} md={12} ld={12}>
            <Typography variant="h1" gutterBottom>
              404
            </Typography>
            <Typography variant="h2" gutterBottom>
              Box Not Found
            </Typography>
          </Grid>
        </Grid>
      }
    </React.Fragment>
  );
}

function SkeletonLoadingHeader() {
  return (
    <React.Fragment>
      <Grid item xs={12}>
        <Skeleton height={100} width="40%" />
      </Grid>
      <Grid item xs={12}>
        <Skeleton height={20} width="60%" />
      </Grid>
    </React.Fragment>
  )
}

function SkeletonLoadingBody() {
  return (
    <React.Fragment>
      <Grid container direction="row" spacing={3}>
        <Grid item xs={4}>
          <Skeleton height={200} width="100%" />
        </Grid>
        <Grid item xs={4}>
          <Skeleton height={200} width="100%" />
        </Grid>
        <Grid item xs={4}>
          <Skeleton height={200} width="100%" />
        </Grid>
      </Grid>
      <Grid container spacing={3}>
        <Grid item xs={4}>
          <Skeleton height={200} width="100%" />
        </Grid>
        <Grid item xs={4}>
          <Skeleton height={200} width="100%" />
        </Grid>
        <Grid item xs={4}>
          <Skeleton height={200} width="100%" />
        </Grid>
      </Grid>
    </React.Fragment>
  )
}
