import { resetWidgetUILayout, setWidgetUILayout } from '@pages/BoxPage/Redux/WidgetUISlice';
import { useAppDispatch, useTypedReduxSelector } from '@store/Store';
import React, { useEffect } from 'react';
import NoWidgetFound from './NoWidgetFound';
import WidgetResponsiveGrid from './WidgetResponsiveGrid';

/**
 * Loading all widget on box happens here.
 * 
 * 
 * @returns JSX
 */
export default function WidgetContainer() {
  const dispatch = useAppDispatch();
  const boxState = useTypedReduxSelector(state => state.box);
  const widgetUIState = useTypedReduxSelector(state => state.widgetUI);


  /**
   *  --------------
   *  Set boxWidget state
   *  --------------
   */
  useEffect(() => {
    if (widgetUIState.widgetsLayout.length !== 0) {
      dispatch(resetWidgetUILayout());
    }

    boxState.boxWidgets.map((widget) => {
      dispatch(setWidgetUILayout(widget.layout));
    });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [boxState.boxWidgets]);


  return (
    <React.Fragment>
      {
        boxState.boxWidgets.length !== 0 && widgetUIState.widgetsLayout.length !== 0 ?
          (
            <WidgetResponsiveGrid initialLayout={widgetUIState.widgetsLayout} />
          )
          : <NoWidgetFound />
      }
    </React.Fragment>
  )
}